.contact-form {
    padding: 0rem 3rem;
    display: flex;
    height: 80vh;
    margin-top: 4rem;
}

.c-right {
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
}

.c-right>form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
}

.c-right .user {
    width: 20rem;
    height: 2rem;
    padding: 0.3rem;
    outline: none;
    border: 2px solid var(--orange);
    border-radius: 8px;
    font-size: 17px;
}

textarea {
    height: 5rem !important;
}

.c-blur1 {
    top: 1rem;
    left: 8rem;
}


@media screen and (max-width: 480px) {
    .contact-form {
        flex-direction: column;
        margin-top: 0;
        padding: 0;
        gap: 1rem;
    }
    .user{
        width: 16rem !important;
    }
}